import React, { Component } from 'react'
import { Container, Row, Col } from 'react-grid-system'
import logo from '../assets/images/LOGO ANIMS icon-03.svg'
import Button from 'react-bootstrap/Button'
class Banner extends Component {

  state = { 
    days:0,
    hours:0,
    minutes:0,
    seconds:0
   }

  componentDidMount(){
    const second = 1000,
      minute = second * 60,
      hour = minute * 60,
      day = hour * 24;

    let countDown = new Date('Mar 02, 2020 00:00:00').getTime()
    
    setInterval(()=> {

      let now = new Date().getTime(),
          distance = countDown - now;

      this.setState({
        days:Math.floor(distance / (day)),
        hours:Math.floor((distance % (day)) / (hour)),
        minutes:Math.floor((distance % (hour)) / (minute)),
        seconds:Math.floor((distance % (minute)) / second)
      })
      //do something later when date is reached
      //if (distance < 0) {
      //  clearInterval(x);
      //  'IT'S MY BIRTHDAY!;
      //}

    }, second)
  }
  render() { 
    const {days, hours, minutes, seconds} = this.state
    return ( 
      <Container fluid>
        <Row className="banner" id="banner">
          <Col md={12} className="contador">
            <Row>
              <Col md={2} xs={2} sm={2}><img src={logo} alt="logo" className="logo-banner"/></Col>
              <Col md={8} xs={10} sm={10}>
                <div className="text-count"> 
                  Next Accelerator Starts the Week of March 02, 2020
                </div>
                <Row justify="center">
                  <Col md={2} xs={3} sm={3} className="count-number">
                    <div>{days}</div> 
                    <div className="title-number">Day(s)</div>
                  </Col>
                  <Col md={2} xs={3} sm={3} className="count-number">
                    <div>{hours}</div> 
                    <div className="title-number">Hour(s)</div>
                  </Col>
                  <Col md={2} xs={3} sm={3} className="count-number">
                    <div>{minutes}</div>
                    <div className="title-number">Minute(s)</div>
                  </Col>
                  <Col md={2} xs={3} sm={3} className="count-number">
                    <div>{seconds}</div> 
                    <div className="title-number">Second(s)</div>
                  </Col>
                </Row>
              </Col>
              <Col md={2} xs={12} sm={12} style={{textAlign:'center'}}><Button variant="primary">Click Here to Join</Button></Col>
            </Row>
          </Col>
          <Col md={12} style={{marginTop:'-1.5em'}}>
            <p className="caligrafia" style={{textAlign:'center'}}>Feeling like a Casualty due to Lack of Communication and  Lack of Skills to Handle Change in the Workplace?</p>
            <div  className="banner-text" style={{textAlign:'center'}}> Create Your Own Personal "Future-Proofing" Plan, with Clear Steps that Cultivate Trust and Get Everyone to Adapt with Confidence.</div>
            {/* <div className="banner-text">Become More Nimble and Confident in <br/> Your Workplace Role and in Your<br/> Capacity to Thrive in an Exponentially<br/> Changing World</div> */}
          </Col>
        </Row>

      </Container>
     );
  }
}
 
export default Banner;