import React, { Component } from 'react';
import { Container, Row, Col } from 'react-grid-system'
import Card from 'react-bootstrap/Card'
import check from '../assets/icons/tick (2)-01.svg'
class Init extends Component {
  state = { 
    text:[
      "Do you feel like you’re constantly in flight or fight mode?",
      "Are you tired of looking over your shoulder, working in an environment where you’re not feeling valued, and where they’ll post your job faster than your obituary?",
      "Or... Perhaps you have barely warmed your seat in your new position, and yet you need to take a break already?",
    
    ]
   }
  render() { 
    return ( 
      <Container fluid>
        <Row className="init" id="init" justify="center">
          <h1 className="title-init">"You've Got to Handle Change First, <br/> Before Change Handles You" <br/><br/>It's all over the internet, and it's the #1 Biggest Myth out there... </h1>
          <p className="text-init">Discover why that approach doesn't actually work (and really, never did), and How this obsession with controlling change, is knocking good people out of the box...</p>
          {
            this.state.text.map((data, i)=>{
              return(
                <Col md={9} key={i}>
                  <Card>
                    <Card.Body style={{display:'flex'}}>
                      <img src={check} alt="check" className="check"/>
                      <div className="data-card">{data}</div>
                    </Card.Body>
                  </Card>
                  <br/>
                </Col>
              )
            })
          }        
        </Row>
      </Container>
     );
  }
}
 
export default Init;